import React, {useState} from "react"
import "./index.css"
import Button from "../Home"

const database = {
  username: "kehinde",
  password: "00711"
}

const Away = () => {
    const [username, setUsername] = useState ("")
    const [password, setPassword] = useState ("")
    const [showError, setError] = useState()

    const submit = () =>{
        if (username !== database.username && password !==database.password){
            setError("you have enter incorrect password or username" ) 
        }else{window.location.href = "/feeds"}
        
    }

    const [getValue,setValue] = useState (0)
    const Increasment = () =>{
        setValue ( getValue + 1)
    }
    const Decreasment = () =>{
        setValue ( getValue -1 )
    }
    
        
    

    return (
        <>
             <div className="main_container">
            <div>{getValue}</div>
            <div>
            <Button value="Increasment" onClick ={ () => Increasment () } />
            <Button value="Decreasment" onClick={ () => Decreasment () } />
            </div>
            <div>
                <div className="">
                    <input type="text" className="input" placeholder="Username"onChange={(e) => setUsername(e.target.value)} />
                </div>
                <div>
                <input type="password"  className="input" placeholder="password"onChange={(e) => setPassword(e.target.value)} />
                </div>
                <div className="result" onClick = { () => submit () } >Login</div>
            </div>
            {showError && <div>{showError} <br></br>
            Username : {username} <br></br>
            Password : {password}
            </div>}

            

            
    </div>
        </>
    ) 
    
}


export default Away